import React, { useState, useEffect } from 'react';
// import axios from 'axios'
import BlogAll from './components/BlogAll'
import BlogForm from './components/BlogForm'
import TopBlogs from './components/TopBlogs'
import blogService from './services/blogs'

const App = (props) => {
  const [blogs, setBlogs] = useState(props.blogs)
  const [newBlog, setNewBlog] = useState('')
  const [newAuthor, setNewAuthor] = useState('')
  const [newUrl, setNewUrl] = useState('')
  // const [votes, setVotes] = useState(0)

  useEffect(() => {
    blogService
      .getAll()
      .then(initialBlogs => {
        setBlogs(initialBlogs)
      })
      .catch(error => {
        alert('There has been an error fetching your blogs.')
      })
  }, [])

  const addNote = (event) => {
    event.preventDefault()
    console.log('Blog added', event.target.value)
      
    const blogObject = {
      title: newBlog,
      author: newAuthor,
      url: newUrl,
      votes: 0,
    }

    blogService
      .create(blogObject)
      .then(returnedBlog => {
        setBlogs(blogs.concat(returnedBlog))
        setNewBlog('')
        setNewAuthor('')
        setNewUrl('')
      })
      .catch(error => {
        alert('Error! Your blog was not added.')
      })
  }

  const handleChangeBlog = (event) => {
    console.log(event.target.value)
    setNewBlog(event.target.value)
  }

  const handleChangeAuthor = (event) => {
    console.log(event.target.value)
    setNewAuthor(event.target.value)
  }

  const handleChangeUrl = (event) => {
    console.log(event.target.value)
    setNewUrl(event.target.value)
  }

  const addVote = (id) => {
    // const url = `http://localhost:3001/blogs/${id}`
    const blog = blogs.find((n) => n.id === id)
    const changedBlog = { ...blog, votes: blog.votes + 1}

    blogService
      .update(id, changedBlog)
        .then(returnedBlog => {
          setBlogs(blogs.map(blog => blog.id !== id ? blog : returnedBlog))
        })
        .catch(error => {
          alert(`Voting error!`)
        })
  } 

  return (
    <div className="mainBody">
      <h1>Blog Logger</h1>
          Enter new found blogs that peak your interest and let others vote on them!
        <BlogForm
          onSubmit={addNote}
          valueBlog={newBlog}
          valueAuthor={newAuthor}
          valueUrl={newUrl}
          handleChangeBlog={handleChangeBlog}
          handleChangeAuthor={handleChangeAuthor}
          handleChangeUrl={handleChangeUrl}
        />

        <h2>Top Rated Blogs</h2>
        <TopBlogs />

        <h2>All Blogs</h2>
        <BlogAll
          blogs={blogs}
          addVote={addVote}
          id={props.id}
          votes={props.votes}
        />
        {/* â€‹<Blog
          blogs={blogs}
          addVote={addVote}
          votes={votes}
        /> */}
    </div>
  )
}

export default App