
import React from 'react'

const Blog = ({ title, author, url, votes, addVote }) => {
  return (
    <div className="Blog">
        <h4>{title}</h4>
        <ul>
          by: {author}
        </ul>
        <ul>
        <a href={url}> {url}</a>
        </ul>
        <ul className="votes">
          {votes} votes
        </ul>
        <ul>
        <button onClick={addVote}>vote</button>          
        </ul>
    </div>
  )
}

export default Blog